@import "./../common/styles/sass/variables";

.aboutMeBlock {
  background-color: $LightBgColor;
  position: relative;
  overflow: hidden;

  .centerLightBorder {
    position: absolute;
    left: 50%;
    width: 1px;
    height: 100%;
    background: $LightBorder;
  }

  .aboutMeContainer {
    padding: 160px 0 160px;
    display: flex;
    align-items: center;
    @media screen and (max-width: 1200px) {
      flex-direction: column;
      padding-bottom: 120px;
    }

    .imageBlock {
      position: relative;
      max-width: 300px;

      .imageBorder {
        position: absolute;
        height: 100%;
        width: 100%;
      }

      &:before,
      &:after {
        content: '';
        position: absolute;
        width: 360px;
        height: 360px;
        top: 20px;
        left: -30px;
        transition: all 0.5s;
      }

      &:before {
        border: 10px solid $lightTextColor;
        transform: rotate(135deg);
      }

      &:after {
        border: 10px solid #555;
        -webkit-transform: rotate(-91deg);
        transform: rotate(-92deg);
      }

      &:hover:before {
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
      }

      &:hover:after {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
      }

      .aboutImage {
        background-image: url("./../common/images/Untitled2.jpeg");
        box-shadow: 0 0 2px 2px $LightBgColor inset;
        background-size: cover;
        width: 300px;
        background-position: center;
        height: 400px;
      }
    }

    .aboutContent {
      flex-direction: column;
      justify-content: center;
      max-width: 400px;
      @media screen and (max-width: 1200px) {
        padding-top: 80px;
        align-items: center;
        text-align: center;
      }

      h2 {
        position: relative;
        color: #ffffff;
        padding-bottom: 15px;
        font-size: 30px;
        display: inline-block;
        margin: 20px 0 20px;

        &:after {
          content: "";
          position: absolute;
          width: 100px;
          height: 2px;
          background: $LightBorder;
          top: 80%;
          left: 0;
          bottom: 0;
        }
      }
      p {
        margin-bottom: 20px;
        color: #ffffff;
        line-height: 1.7em;

        &:nth-child(3) {
          padding-top: 40px;
          padding-bottom: 40px;
        }
      }
    }
  }

  &:before {
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: 1px;
    background: $LightBorder;
  }
}

